import React, { useState } from "react";
import { Link, Routes, Route, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import "./style.css";

function SignUp() {
  const [nickname, setNickname] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [passwordsMatch, setPasswordsMatch] = useState(true); //    
  const [isRegistered, setIsRegistered] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    //     
    if (password === confirmPassword) {
      try {
        const response = await axios.post(
          "https://api.rainbow-letter.com/join",
          {
            email,
            password,
            nickname,
          }
        );

        //      
        console.log(response.data);
        console.log("ndickname: ", nickname);
        console.log("email: ", email);
        console.log("password: ", password);

        //    
        setIsRegistered(true);

        //    -   ?
        toast.success(" ");
        navigate("/pages/Login");
      } catch (error) {
        //      
        console.error(" :", error);
        // ** -    ?
      }
    } else {
      setPasswordsMatch(false); //     
    }
  };

  return (
    <SignupWrapper>
      <img src="https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/signup.png" />
      <h1 id="signupHeadText"></h1>
      <h3>    !</h3>
      <form onSubmit={handleSubmit}>
        <SignInput>
          <Input
            type="text"
            id="nickname"
            value={nickname}
            placeholder=""
            onChange={(e) => setNickname(e.target.value)}
            autoComplete="off"
            required
          />
        </SignInput>
        <SignInput>
          <Input
            type="email"
            id="email"
            value={email}
            placeholder=""
            onChange={(e) => setEmail(e.target.value)}
            autoComplete="off"
            required
          />
        </SignInput>
        <SignInput>
          <Input
            type="password"
            id="password"
            value={password}
            placeholder=""
            onChange={(e) => setPassword(e.target.value)}
            autoComplete="off"
            required
          />
        </SignInput>
        <SignInput>
          <Input
            type="password"
            id="confirmPassword"
            value={confirmPassword}
            placeholder=" "
            onChange={(e) => setConfirmPassword(e.target.value)}
            autoComplete="off"
            required
          />
        </SignInput>
        {!passwordsMatch && (
          <p style={{ color: "red" }}>  .</p>
        )}
        <button id="SignButton" type="submit">
          
        </button>
      </form>
      <StyledLink to="/pages/Login">  ?</StyledLink>
    </SignupWrapper>
  );
}
const SignupWrapper = styled.div`
  margin: auto;
  padding: 50px;
  width: 1000px;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  img {
    width: 180px;
  }
  h3 {
    color: #1f434e;
    /*margin-bottom: 50px;*/
  }
`;
const StyledLink = styled(Link)`
  text-decoration: none;
  color: #0f2f36;
  &:hover {
    color: black;
    font-weight: 500;
  }
`;
const Input = styled.input`
  padding: 20px;
  width: 300px;
  height: 50px;
  font-size: 15px;
  border: 1px solid #dde7ef;
  border-radius: 0px;
`;
const SignInput = styled.div`
  display: flex;
  /*margin-bottom: 10px;*/
`;

export default SignUp;
