import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import "../pages/style.css";

function TutorialDiary() {
  return (
    <TutorialCard>
      <h4> </h4>
      <p>
           !
        <br />
            
        <br />
            .
      </p>
      <br /> <br />
      <Explanation>
        <img src="/images/books.png" />
        <TextExplanation>
          <h5>   </h5>
          <p>
            1.    . <br />
            2.      
            <br />
            . <br />
            3.     <br />
                . <br />
            4.     <br />
                 
            <br />  .  ! <br />
            5.    ,
            <br />   ! <br />
            6.    . <br />
              ! <br />
          </p>
        </TextExplanation>
      </Explanation>
    </TutorialCard>
  );
}

const TutorialCard = styled.div`
  margin: 10px;
  padding: 10px;
  width: 370px;
  height: 460px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 3px solid var(--secondary-color, #ffa4a1);
  color: black;
  h4 {
    margin: 0 auto;
    margin-bottom: 5px;
    font-size: 25px;
  }
  p {
    margin: 0 auto;
    font-size: 15px;
  }
`;
const Explanation = styled.div`
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  img {
    margin-top: 80px;
    width: 120px;
    height: 144.38px;
  }
`;
const TextExplanation = styled.div`
  display: flex;
  flex-direction: column;
  h5 {
    font-size: 15px;
  }
`;

export default TutorialDiary;
