import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { styled, css } from "styled-components";
import "./style.css";

function Brand() {
  const navigate = useNavigate();
  const [scrollPercentage, setScrollPercentage] = useState(0);
  const [showTopButton, setShowTopButton] = useState(false);
  const firstPic =
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landing1.png";
  const secondPic =
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landing2.png";
  const thirdPic =
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landing3.png";
  const fourthPic =
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landing4.png";
  const fifthPic =
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landing5.png";
  const landingWallpaper =
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landingback.png";

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 200) {
        setShowTopButton(true);
      } else {
        setShowTopButton(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const handleScroll = () => {
    const targetElement = document.getElementById("targetElement");
    if (targetElement) {
      targetElement.scrollIntoView({ behavior: "smooth" });
    }
  };

  return (
    <>
      <ProjectInfo>
        <img
          src="https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/logo.png"
          alt="Rainbow letter Logo"
          onClick={handleScroll}
        />
        <h1>   , </h1>
        <p> !</p>
      </ProjectInfo>
      <GradientBackground />
      <Content>
        <img src="https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/rocket.png" />
      </Content>
      <Wallpaper id="targetElement">
        <Wrapper>
          <PageWrapper id="first">
            <TextContainer style={{ marginBottom: "80px" }}>
              <h1>
                  <br />
                  
              </h1>
              <p>
                      <br />
                      .
              </p>
            </TextContainer>
            <ImgContainer>
              <img
                src={firstPic}
                style={{ width: "250px", marginLeft: "100px" }}
              />
            </ImgContainer>
          </PageWrapper>
          <PageWrapper id="second">
            <ImgContainer>
              <img
                src={secondPic}
                style={{ width: "280px", marginRight: "100px" }}
              />
            </ImgContainer>
            <TextContainer>
              <h1>
                 
                <br />
                 
              </h1>
              <p>
                       
                <br />
                  ,   
                <br />
                  .
              </p>
            </TextContainer>
          </PageWrapper>
          <PageWrapper id="third">
            <TextContainer>
              <h1>
                  <br />
                 
              </h1>
              <p>
                   
                <br />
                     .
              </p>
            </TextContainer>
            <ImgContainer>
              <img
                src={thirdPic}
                style={{ width: "270px", marginLeft: "100px" }}
              />
            </ImgContainer>
          </PageWrapper>
          <PageWrapper id="fourth">
            <ImgContainer>
              <img
                src={fourthPic}
                style={{ width: "280px", marginRight: "100px" }}
              />
            </ImgContainer>
            <TextContainer>
              <h1>
                  <br />
                
              </h1>
              <p>
                   
                <br />
                  .
              </p>
            </TextContainer>
          </PageWrapper>
          <PageWrapper id="fifth">
            <TextContainer id="lastBrand">
              <h1>
                    <br />
                 
              </h1>
              <p>
                    .
                <br />
                     .
              </p>
              <button
                id="startBrand"
                onClick={() => navigate("/pages/Persona")}
              >
                
              </button>
            </TextContainer>
            <ImgContainer>
              <img src={fifthPic} style={{ height: "300px" }} />
            </ImgContainer>
          </PageWrapper>
          <div className="scroll-buttons">
            {showTopButton && (
              <button className="top-button" onClick={scrollToTop}>
                Top
              </button>
            )}
          </div>
        </Wrapper>
      </Wallpaper>
      <div className="scroll-buttons">
        {showTopButton && (
          <button className="top-button" onClick={scrollToTop}>
            Top
          </button>
        )}
      </div>
    </>
  );
}

const Wallpaper = styled.div`
  margin: 0;
  width: 100vw;
  height: 4833px;
  background-image: url("https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/frontend/landingback.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @media (max-width: 768px) {
    width: 768px;
    heihgt: 1933.45px;
  }
`;
const ProjectInfo = styled.div`
  height: 100vh;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h1 {
    color: black;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  p {
    color: #000027;
    font-size: 18px;
  }
  img {
    width: 100px;
    height: auto;
    border-radius: 10px;
    cursor: pointer;
    filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.25));
  }
  @media (max-width: 768px) {
    width: 768px;
  }
`;
const GradientBackground = styled.div`
  height: 600px;
  background: linear-gradient(
      180deg,
      #fff 0%,
      #f5f5f5 10%,
      #b2b2b2 30%,
      #5a5a73 50%,
      #000027 80%,
      #000027 95%
    ),
    #fff;
  margin-bottom: -1px;
  @media (max-width: 768px) {
    width: 768px;
  }
`;
const Content = styled.div`
  padding: 50px 0;
  height: 500px;
  text-align: center;
  font-size: 24px;
  color: white;
  background-color: #000027;
  margin-bottom: -22px;
  img {
    width: 350px;
  }
  @media (max-width: 768px) {
    width: 768px;
  }
`;
const Wrapper = styled.div`
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  @media (max-width: 768px) {
    width: 768px;
  }
`;
const PageWrapper = styled.div`
  display: flex;
  flex-direction: row;
  height: 700px;
  justify-content: center;
  align-items: center;
  @media (max-width: 768px) {
    width: 768px;
  }
`;
const ImgContainer = styled.div`
  justify-content: center;
  align-items: center;
  @media (max-width: 768px) {
    width: 768px;
  }
`;
const TextContainer = styled.div`
  width: auto;
  justify-content: center;
  align-items: center;
  h1 {
    font-size: 35px;
    font-weight: 800;
    margin-bottom: 15px;
  }
  p {
    font-size: 18px;
    font-weight: 400;
  }
  @media (max-width: 768px) {
    h1 {
      font-size: 20px;
    }
    p {
      font-size: 15px;
    }
  }
`;
/* 
  

const Wallpaper = styled.div`
  top: 0;
  left: 0;
  z-index: -1;
  position: fixed;
  width: 100vw;
  height: 4000px;
  background-image: url(/images/landingback.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
`;
const TextContainer = styled.div`
  width: auto;
  margin-bottom: 100px;
`;
*/

export default Brand;
