import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import TextField from "@mui/material/TextField";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import dayjs from "dayjs";
import { DemoContainer, DemoItem } from "@mui/x-date-pickers/internals/demo";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import styled from "styled-components";
import axios from "axios";
import "../style.css";
// import Finished from "../../components/Persona/Finished";

function PersonaDog() {
  const speciesName = "";
  const [name, setName] = useState("");
  const [ownerName, setOwnerName] = useState("");
  const [charOne, setCharOne] = useState("");
  const [charTwo, setCharTwo] = useState("");
  const [favoritePlay, setFavoritePlay] = useState("");
  const [customPlay, setCustomPlay] = useState("");
  const [favoriteSnack, setFavoriteSnack] = useState("");
  const [favoriteTime, setFavoriteTime] = useState("");
  const [habit, setHabit] = useState("");
  const [favoritePlace, setFavoritePlace] = useState("");
  const [routine, setRoutine] = useState("");
  const [petPhoto, setPetPhoto] = useState("");
  const [passed_date, setPassedDate] = useState(dayjs());
  const [furColor, setFurColor] = useState("");
  const [kind, setKind] = useState("");
  const [showTopButton, setShowTopButton] = useState(false);
  const token = localStorage.getItem("token");
  const navigate = useNavigate();

  const [objUrl, setObjUrl] = useState(null); // URL  
  const [uploadUrl, setUploadUrl] = useState(null); //presigned URL -  url
  const [sendingUrl, setSendingUrl] = useState(
    "https://hack-s3bucket.s3.ap-northeast-2.amazonaws.com/petprofile/pf_dog.png"
  );

  useEffect(() => {
    const fetchUploadUrl = async () => {
      try {
        //    URL 
        const response = await axios.get(
          `https://api.rainbow-letter.com/image?dirname=petprofile`,
          {
            headers: {
              "X-ACCESS-TOKEN": `Bearer ${token}`,
            },
          }
        );
        setUploadUrl(response.data);
        console.log("  URL:", response.data);
        console.log("  ():", sendingUrl);
      } catch (error) {
        console.error("  URL  ", error);
      }
    };
    fetchUploadUrl();
  }, [token]);

  useEffect(() => {
    if (objUrl) {
      const desiredUrl = objUrl.split("?")[0];
      console.log(" URL:", desiredUrl);
      setSendingUrl(desiredUrl);
    }
  }, [objUrl]);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 200) {
        setShowTopButton(true);
      } else {
        setShowTopButton(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0]; //  
    const reader = new FileReader();
    reader.onloadend = () => {
      setPetPhoto(reader.result);
    };
    if (file) {
      reader.readAsDataURL(file); //  data URL 
      uploadImageToS3(uploadUrl, file);
    }
  };

  function uploadImageToS3(uploadUrl, file) {
    axios
      .put(uploadUrl, file, {
        headers: {
          "Content-Type": "image/png",
        },
      })
      .then((response) => {
        console.log("S3   response: ", response);
        setObjUrl(response.config.url);
      })
      .catch((error) => console.error("S3  ", error));
  }

  //   
  const goBack = () => {
    navigate(-1);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    //       
    if (!name || !ownerName) {
      alert("   .");
      return;
    }

    try {
      const formData = new FormData();
      formData.append("speciesName", speciesName);
      formData.append("name", name);
      formData.append("charOne", charOne);
      formData.append("charTwo", charTwo);
      formData.append("ownerName", ownerName);
      formData.append("favoritePlay", favoritePlay);
      formData.append("favoriteSnack", favoriteSnack);
      formData.append("favoriteTime", favoriteTime);
      formData.append("habit", habit);
      formData.append("favoritePlace", favoritePlace);
      formData.append("routine", routine);
      /*
      if (desriedUrl) {
        formData.append("petImage", desiredUrl);
      } else {
        formData.append("petImage", sendingUrl);
      }*/
      formData.append("petImage", sendingUrl);
      formData.append("passed_date", passed_date);
      /*const formattedDate = passed_date.toISOString(); // Date  ISO   
        formData.append("passed_date", formattedDate);*/
      formData.append("furColor", furColor);
      formData.append("kind", kind);

      const formDataObject = {};
      formData.forEach((value, key) => {
        formDataObject[key] = value;
      });
      const jsonData = JSON.stringify(formDataObject);
      console.log(jsonData);

      const response = await axios.post(
        "https://api.rainbow-letter.com/persona/save",
        jsonData,
        {
          headers: {
            "X-ACCESS-TOKEN": `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        }
      );
      alert("  !");
      navigate("/pages/Persona");
      console.log("  !");
    } catch (error) {
      console.error("  :", error);
      //    
    }
  };

  return (
    <Wrapper>
      <Container id="firstPersonaSet">
        <ImgContainer>
          <img
            src="/images/dog1.png"
            style={{ width: "280px", marginRight: "70px" }}
          />
        </ImgContainer>
        <InputContainer>
          <h3>  ?</h3>
          <br />
          <TextField
            required
            label=" "
            variant="outlined"
            size="small"
            value={name}
            onChange={(e) => setName(e.target.value)}
            autoComplete="off"
          />
          <br />
          <br />
          <h3>
               <br />
             .
          </h3>
          <br />
          <TextField
            required
            label=" "
            variant="outlined"
            size="small"
            helperText=", ,  "
            value={ownerName}
            onChange={(e) => setOwnerName(e.target.value)}
            autoComplete="off"
          />
        </InputContainer>
      </Container>

      <Container id="secondPersonaSet">
        <InputContainer>
          <h3>  .</h3>
          <br />
          <br />
          <InputLabel id="firstChar">  </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={charOne}
              onChange={(e) => setCharOne(e.target.value)}
              displayEmpty
            >
              <MenuItem value="" disabled>
                <em>   </em>
              </MenuItem>
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="   ">   </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="  ">  </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
          <br />
          <InputLabel id="secondChar">  </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={charTwo}
              onChange={(e) => setCharTwo(e.target.value)}
              displayEmpty
            >
              <MenuItem value="" disabled>
                <em>   </em>
              </MenuItem>
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="   ">   </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="  ">  </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
        </InputContainer>
        <ImgContainer>
          <img
            src="/images/dog2.png"
            style={{ width: "380px", marginLeft: "100px", marginTop: "30px" }}
          />
        </ImgContainer>
      </Container>

      <Container id="thirdPersonaSet">
        <ImgContainer id="PersonaImageCat">
          <img
            src="/images/dog6.png"
            style={{
              width: "250px",
              marginBottom: "110px",
              marginRight: "70px",
            }}
          />
        </ImgContainer>
        <InputContainer>
          <h3>  ?</h3>
          <br />
          <br />
          <InputLabel id="favoritePlay"> </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={favoritePlay}
              onChange={(e) => setFavoritePlay(e.target.value)}
              displayEmpty
            >
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value="  ">  </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="  ">  </MenuItem>
            </Select>
          </FormControl>

          <br />

          <InputLabel id="favoriteSnack"> </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={favoriteSnack}
              onChange={(e) => setFavoriteSnack(e.target.value)}
              displayEmpty
            >
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>

          <br />

          <InputLabel id="favoriteTime"> </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={favoriteTime}
              onChange={(e) => setFavoriteTime(e.target.value)}
              displayEmpty
            >
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>

          <br />

          <InputLabel id="favoritePlace"> </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={favoritePlace}
              onChange={(e) => setFavoritePlace(e.target.value)}
              displayEmpty
            >
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
            </Select>
          </FormControl>

          <br />

          <InputLabel id="habit">  </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={habit}
              onChange={(e) => setHabit(e.target.value)}
              displayEmpty
            >
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="  ">  </MenuItem>
              <MenuItem value="None">()</MenuItem>
            </Select>
          </FormControl>

          <br />

          <InputLabel id="routine"> </InputLabel>
          <FormControl fullWidth variant="outlined" size="small">
            <Select
              variant="outlined"
              value={routine}
              onChange={(e) => setRoutine(e.target.value)}
              displayEmpty
            >
              <MenuItem value={null}>-- --</MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value="   ">
                   
              </MenuItem>
              <MenuItem value=" "> </MenuItem>
              <MenuItem value="None">()</MenuItem>
            </Select>
          </FormControl>
        </InputContainer>
      </Container>

      <Container id="fourthPersonaSet">
        <InputContainer>
          <h3>
               <br />
              .
          </h3>
          <br />
          <br />
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <DemoContainer components={["DatePicker"]}>
              <DatePicker
                label=" "
                value={passed_date}
                onChange={(passed_date) => setPassedDate(passed_date)}
              />
            </DemoContainer>
          </LocalizationProvider>
          <br />
          <br />
        </InputContainer>
        <ImgContainer>
          <img
            src="/images/dog7.png"
            style={{ width: "260px", marginLeft: "80px" }}
          />
        </ImgContainer>
      </Container>

      <Container id="fifthPersonaSet">
        <ImgContainer>
          <img
            src="/images/dog4.png"
            style={{ width: "300px", marginTop: "10px", marginRight: "40px" }}
          />
        </ImgContainer>
        <InputContainer>
          <h3>  ?</h3>
          <br />
          <br />
          <TextField
            label=" (species)"
            variant="outlined"
            size="small"
            value={kind}
            onChange={(e) => setKind(e.target.value)}
            autoComplete="off"
          />
          <br />
          <TextField
            label=" "
            variant="outlined"
            size="small"
            value={furColor}
            onChange={(e) => setFurColor(e.target.value)}
            autoComplete="off"
          />
        </InputContainer>
      </Container>

      <br />

      <Container id="sixthPersonaSet">
        <InputContainer>
          <h3>   .</h3>
          <br />
          <p>
                . <br />
                .
          </p>
          <br />
          <br />
          <ImgUpload>
            <input type="file" onChange={handleImageChange} />
          </ImgUpload>
        </InputContainer>
        <ImgContainer>
          <img
            src="/images/dog12.png"
            style={{ width: "230px", marginLeft: "60px" }}
          />
        </ImgContainer>
      </Container>
      <ButtonContainer>
        <CancelButton id="backBtn" onClick={goBack}>
          
        </CancelButton>
        <DoneButton id="doneBtn" onClick={handleSubmit}>
          !
        </DoneButton>
      </ButtonContainer>
      <div className="scroll-buttons">
        {showTopButton && (
          <button className="top-button" onClick={scrollToTop}>
            Top
          </button>
        )}
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  margin: auto;
  width: 900px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const Container = styled.div`
  margin: 100px auto;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
`;
const ImgContainer = styled.div`
  display: flex;
  justify-content: flex-end;
`;
const InputContainer = styled.div`
  margin: 0;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  p {
    font-size: 15px;
  }
`;
const ImgUpload = styled.div`
  display: flex;
  width: 191px;
  height: 165px;
  padding: 0px 26px 0px 27px;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  border: 2px dashed var(--Faded, rgba(0, 0, 0, 0.15));
  background: #fff;
`;
const ButtonContainer = styled.div``;

const CancelButton = styled.button`
  position: fixed;
  bottom: 20px;
  transform: translateX(-110%);
  margin: 10px;
  width: 140px;
  height: 50px;
  font-weight: bold;
  font-size: 18px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 56px;
  border: 2px solid var(--Dark-Blue, #0a2640);
  background: white;
  cursor: pointer;
`;
const DoneButton = styled.button`
  position: fixed;
  bottom: 20px;
  transform: translateX(10%);
  margin: 10px;
  width: 140px;
  height: 50px;
  font-weight: bold;
  font-size: 18px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border: none;
  border-radius: 56px;
  color: white;
  background: rgba(0, 0, 0, 0.83);
  cursor: pointer;
`;
export default PersonaDog;
